import "./MyHand.css";
import { Card } from './Card.js';
import { useState } from 'react';
import { useEffect, useRef } from "react";

export function MyHand(props) {

  // Props:
  // hand - array of card values

  console.log(`myhand called with props.hand: ${props.hand}`)
  console.log(`myhand called with props.playable: ${props.playable}`)
  console.log(`myhand called with props.yourGo: ${props.yourGo}`)

  const [hand, setHand] = useState([])
  const [playableClass, setPlayableClass] = useState("myhand-non-playable-knock")

  const handsRef = useRef()

  useEffect( () => {

    console.log("setting hand")

    if (props.hand && handsRef.current !== props.hand ) {
      setHand(h => {
        return props.hand.map( xcard => { 
          let playable = false
          let index = props.playable.findIndex(c1 => {
            return c1 === xcard
          })
          if (index !== -1) playable = true 
          let r = {value: xcard, selected: false, playable: playable}
          return r
        })  
      })
    }

    handsRef.current = props.hand

  },[props.hand, props.playable])
  
  useEffect( () => {

    console.log("applying props.playable")
    if (props.playable) {
      setHand(h => {
        return h.map( xcard => {
          let playable = false
          let index = props.playable.findIndex(c1 => {
            return c1 === xcard.value
          })
          if (index !== -1) playable = true 
          let r = {value: xcard.value, selected: xcard.selected, playable: playable}
          return r
        })  
      })
    }

  }, [props.playable])

  const restoreCardRef = useRef()

  useEffect( () => {

    console.log("restoring card")

    const cardpack = [
      "AH","2H","3H","4H","5H","6H","7H","8H","9H","10H","JH","QH","KH",
      "AC","2C","3C","4C","5C","6C","7C","8C","9C","10C","JC","QC","KC",
      "AD","2D","3D","4D","5D","6D","7D","8D","9D","10D","JD","QD","KD",
      "AS","2S","3S","4S","5S","6S","7S","8S","9S","10S","JS","QS","KS"
    ]  

    if (props.restoreCard && restoreCardRef.current !== props.restoreCard) {
      setHand(h => {
        let newHand = [...h, { value: props.restoreCard, selected: false, playable: false}]
        newHand.sort( (a,b) => {
          let ai = cardpack.findIndex(c => a.value === c)
          let bi = cardpack.findIndex(c => b.value === c)
          return (ai - bi)
        })

        let newHandP = newHand.map(cardP => {
          let index = props.playable.findIndex(c1 => {
            return c1 === cardP.value
          })
          let ret = {...cardP}
          if (index === -1) ret.playable = false 
          else ret.playable = true
          return ret
        })
  
        return newHandP
      })
    }

    restoreCardRef.current = props.restoreCard

  },[props.restoreCard, props.playable])

  function toggleSelected(id) {

    let newHand = hand.map( (card,index) => {
      return { value: card.value, playable: card.playable, selected: id === index ? true : false}
    })

    setHand(newHand)
  }

  function playCard() {
    let selected = hand.findIndex( c => {
      return (c.selected && c.playable)
    })

    console.log(`selected: ${selected}`)

    if (selected >= 0) {
      let card = hand[selected].value

      let newHand = [...hand];
      newHand.splice(selected,1);
  
      props.playCard(card,newHand.length)
  
      setHand(newHand)  
    } else {
      alert("You must select a card first")
    }
  }

  function knock() {

    let playableCards = false;

    hand.forEach(xcard => {
      let index = props.playable.findIndex(pcard => {
        return pcard === xcard.value
      })

      if (index !== -1) {
        playableCards = true
      }
    })

    if (playableCards) {
      setPlayableClass("myhand-playable-knock bounce")      
    } else {
      setPlayableClass("myhand-non-playable-knock")
      props.knock()
    }
  }

  return (
    <div className="myhand-panel">
      <div className="myhand-container">
        { hand.length > 0
          && hand.map( (cardx,index) => {
            return <div key={index} className="myhand-card">
              <Card playable={cardx.playable} value={cardx.value} selected={cardx.selected} onClick={()=>{toggleSelected(index)}}/>
            </div>
            })
        }
        { props.yourGo &&
        <div class="myhand-play-buttons">
          <input type="button" onClick={() => {playCard()}} value="Play Card" />
          <input type="button" className={playableClass} onClick={() => {knock()}} value="Knock" />
          <span>Your go!</span>
        </div>        
        }
      </div>
    </div>
  )
}