import './App.css';
import { MyHand } from './MyHand.js';
import { CardTable } from './CardTable.js';
import { useState, useEffect } from 'react';
import * as CardUtils from './card-utils';
import axios from 'axios';

function App() {

  console.log("App called")

  const [restoreCard,setRestoreCard] = useState()
  const [log,setLog] = useState("")
  const [hand,setHand] = useState([])
  const [player,setPlayer] = useState()
  const [nextPlayer,setNextPlayer] = useState('start')
  const [game,setGame] = useState(resetGame())
  const [ws,setWs] = useState()
  const [playable,setPlayable] = useState(['7D'])
  const [kitty,setKitty] = useState(0)
  const [knocks,setKnocks] = useState({})
  const [echo,setEcho] = useState(Date.now())
  const [startTime,setStartTime] = useState(Date.now())
  
  console.log(`Player: ${player}`)

  function resetGame() { return {
    "hearts": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "clubs": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "diamonds": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "spades": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ]
    }
  }

  useEffect( () => {

    // runs once on component initialisation
    console.log("App: setting up websocket")

    let hostname = window.location.hostname;

    var HOST = "wss://" + hostname
    let localWs = new WebSocket(HOST);

    localWs.onopen = (evt) => {
      console.log(`Websocket opened`)
    }

    localWs.onclose = (evt) => {
      console.log('Websocket closed!')
      alert("Connection closed....")
    }

    // setup ping to keep connection open

    setInterval( () => {
      localWs.send(
        JSON.stringify({
          type: 'ping'
        })          
      )
      axios.get(`https://${hostname}/api/poll`);
      console.log("http GET performed");
    },30000) 

    setWs(localWs)

  },[])

  useEffect( () => {

    // helper functions for the event handling must be in the scope of the useEffect,
    // otherwise they will pick up stale references to state variables

    function updateGame(card,action) {
      setGame( g => {
  
        let suit = card.length === 2 ? card.charAt(1) : card.charAt(2);
        let cardValue = card.length === 2 ? card.substring(0,1) : card.substring(0,2)
  
        let played = true;
  
        if (action === 'play') {
          played = true 
        } else if ( action === 'retract') {
          played = false
        }
    
        const newTable = {
          hearts: g["hearts"].map( oldcard => {
            if (suit === "H" && cardValue === oldcard.value) return { value: oldcard.value, played: played}
            else return { value: oldcard.value, played: oldcard.played}
          }),
          clubs: g["clubs"].map( oldcard => {
            if (suit === "C" && cardValue === oldcard.value) return { value: oldcard.value, played: played}
            else return { value: oldcard.value, played: oldcard.played}
          }),
          diamonds: g["diamonds"].map( oldcard => {
            if (suit === "D" && cardValue === oldcard.value) return { value: oldcard.value, played: played}
            else return { value: oldcard.value, played: oldcard.played}
          }),
          spades: g["spades"].map( oldcard => {
            if (suit === "S" && cardValue === oldcard.value) return { value: oldcard.value, played: played}
            else return { value: oldcard.value, played: oldcard.played}
          })
        }

        // now work out playable cards...

        let s = ["hearts","clubs","diamonds","spades"]
        let sChars = ["H","C","D","S"]

        let playableCards = []

        s.forEach((suit1,index) => {
          if (newTable[suit1][6].played === false) playableCards.push(newTable[suit1][6].value + sChars[index])
          else {
            for (let i=7; i<13; i++) {
              if (newTable[suit1][i].played === false) {
                playableCards.push(newTable[suit1][i].value + sChars[index])
                break
              }
            }
            for (let i=5; i>=0; i--) {
              if (newTable[suit1][i].played === false) {
                playableCards.push(newTable[suit1][i].value + sChars[index])
                break
              }
            }
          }
  
        })

        setPlayable(playableCards)
      
        return(newTable)    
      })
    }
    
    function newGame() {
      console.log(`New game called, player: ${player}`)
      setKitty(0)
      setNextPlayer('start')
      setLog(`New game! - who has the the 7 of Diamonds?`)
      setGame(resetGame())
      sendHandRequest(player)
      setPlayable(["7D"])
    }  

    function updateHand(newhand) {
      console.log(`Update hand called with ${newhand}`)
  
      newhand.sort( (a,b) => {
        let ai = CardUtils.cardpack.findIndex(c => a === c)
        let bi = CardUtils.cardpack.findIndex(c => b === c)
        return (ai - bi)
      })
  
      setHand(newhand)
    }

    function sendHandRequest(p) {
      console.log(`send hand request called, player: ${p}`)
      if (p) {
        ws.send(JSON.stringify({
          type: 'hand',
          player: p
        }))  
      }
    }
    
    // websocket event handlers

    if (ws) {
      ws.onmessage = (event) => {
        console.log(event.data)
        let m = JSON.parse(event.data);
        let np;
  
        switch(m.type) {
          case 'reset':
            setPlayer('')
            resetGame()
            setHand([])
            break;
          case 'card':
            np = m.nextplayer === player ? "YOUR" : `${m.nextplayer}'s`
            setNextPlayer(m.nextplayer)
            if (m.left === 0) {
              setLog(`${m.player} played ${getCard(m.card)} and has WON!!!!!!!`)
            } else {
              setLog(`${m.player} played ${getCard(m.card)} and has ${m.left} card(s) left - ${np} go...`)
            }
            updateGame(m.card,'play');
            break;
          case 'knock':
            np = m.nextplayer === player ? "YOUR" : `${m.nextplayer}'s`
            setNextPlayer(m.nextplayer)
            setLog(`${m.player} knocked! - ${np} go...`)
            break;
          case 'retractCard':
            let value = CardUtils.getCardValue(m.card)
            let suit = CardUtils.getSuitSymbol(m.card)
            setLog(`${m.player} retracted card ${value + suit}`)
            updateGame(m.card,'retract')
            setNextPlayer(m.player)
            break;
          case 'message':
            setLog(`${m.message}`)
            break;
          case 'newgame':
            newGame();
            break;
          case 'kitty':
            setKitty(m.kitty)
            setKnocks(m.knocks)
            break;
          case 'hand':
            updateHand(m.cards);
            break;
          case 'echo':
            setEcho(Date.now())
            break;
          default:
            console.log("unsupported message type")
        }
      }  
    }
  }, [ws,player])

  function sendNewGame() {
    ws.send(JSON.stringify({
      type: 'newgame'
    }))  
  }

  function sendCard(card,left) {
    setRestoreCard("")
    ws.send(JSON.stringify({
      type: 'card',
      player: player,
      left: left,
      card: card
    }))
  }

  function getCard(card) {
    let value = CardUtils.getCardValue(card)
    let suit = CardUtils.getSuitSymbol(card)
    return (value + suit)
  }

  function sendReset() {
    ws.send(JSON.stringify({
      type: 'reset'
    }))
  }

  function sendKitty() {
    ws.send(JSON.stringify({
      type: 'kitty'
    }))
  }

  function knock() {
    ws.send(JSON.stringify({
      type: 'knock',
      player: player
    }))
    sendKitty()
  }

  function removeCard(card) {
    if(window.confirm(`Are you sure you want to retract the ${card}?`)){ 
      setRestoreCard(card)
      ws.send(JSON.stringify({
        type: 'retractCard',
        player: player,
        card: card
      }))    
    }
  }

  function showKitty() {
    if ( kitty && knocks.players )
    {
      alert(
        "Kitty: " + kitty + "p\n" 
        + knocks.players[0] + " knocked " + knocks.knocks[0] + " times\n"
        + knocks.players[1] + " knocked " + knocks.knocks[1] + " times\n"
        + knocks.players[2] + " knocked " + knocks.knocks[2] + " times\n"
        + knocks.players[3] + " knocked " + knocks.knocks[3] + " times\n"
      )
  
    }
  }

  // Player registration

  let playerText = ""

  function updatePlayerText(evt) {
    playerText = evt.target.value
  }

  function registerNewName() {
    register(playerText)
  }

  function playerChanged(evt) {
    if (evt.target.value !== "...") {
      register(evt.target.value)
    }
  }

  function register(p) {
    ws.send(JSON.stringify({
      type: 'register',
      player: p
    }))
    setPlayer(p) 
  }

  const presetPlayers = [
    "...","John","Nancy","Allen","Gavin","Eric"
  ]

  const playerOptions = presetPlayers.map((p) =>
  <option value={p}>{p}</option>)

  console.log(`App: player = ${player}, nextPlayer = ${nextPlayer}`)

  let echoTime = Math.round((echo - startTime)/1000)

  
  return (
    <div className="App">
      { !player &&
        <div className="app-register">
          <h1>Sevens!</h1>
          <label htmlFor="players">Choose a predefined player:</label>
          <select value={player} name="players" id="players" onChange={playerChanged}>
            {playerOptions}
          </select>
          <div>...or enter a different name:</div>
          <input type="text" onChange={updatePlayerText} />
          <input type="button" value="Register new name" onClick={registerNewName} />
        </div>
      }
      { player &&
          <div className="app-controls-and-hand">
          <div className="app-controls">
            { player === "John" &&
            <div className="app-container">
              <div className="app-playerselecter">
                <div><span>{echoTime}</span></div>
                <div><input type="button" onClick={sendNewGame} value="New Game" /></div>
                <div><input type="button" onClick={showKitty} value="Kitty" /></div>
                <div><input type="button" onClick={sendReset} value="Reset Session" /></div>
              </div>
            </div>          
            }
            <div>
              <div className="app-log">{log} - kitty: {kitty}p</div>
            </div>
          </div>

          <MyHand yourGo={nextPlayer === player || nextPlayer === 'start'} playable={playable} hand={hand} playCard={sendCard} knock={knock} restoreCard={restoreCard}/> 
        </div>
      }
      { player &&
        <CardTable game={game} remove={removeCard}/>      
      }
    </div>
  );
}

export default App;
